import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BaseUrl } from 'src/app/enums/baseUrl';
import { Observable } from 'rxjs';
// import { IRequest } from 'src/app/models/Request';
import { PatientsInfo , CreateOrEditPatientDto, GetPatientForEditOutput } from 'src/app/models/Patients'

export interface Request{
  error: string | undefined;
  result: IItems | undefined;
  success:  boolean | undefined;
  targetUrl: string | undefined;
  unAuthorizedRequest: boolean | undefined;
  }

  export interface IItems {
    totalCount : number | undefined;
    items: PatientsInfo [] | undefined;
  }


@Injectable({
  providedIn: 'root'
})
export class PatientsService {

  constructor(
    private http : HttpClient
  ) { }

   /**
     * @param filter (optional) 
     * @param userId (optional) 
     * @param sorting (optional) 
     * @param skipCount (optional) 
     * @param maxResultCount (optional) 
     * @return Success
     */
    getAllPatients(filter: string | null | undefined, userId: number | null | undefined, sorting: string | null | undefined, skipCount: number | null | undefined, maxResultCount: number | null | undefined  ): Observable<Request> {
      let url_ =  BaseUrl.baseUrl + "/api/services/app/Patients/GetAll?";


      if (filter !== undefined)
          url_ += "Filter=" + encodeURIComponent("" + filter) + "&"; 
      if (userId !== undefined)
          url_ += "UserId=" + encodeURIComponent("" + userId) + "&"; 
      if (sorting !== undefined)
          url_ += "Sorting=" + encodeURIComponent("" + sorting) + "&"; 
      if (skipCount !== undefined)
          url_ += "SkipCount=" + encodeURIComponent("" + skipCount) + "&"; 
      if (maxResultCount !== undefined)
          url_ += "MaxResultCount=" + encodeURIComponent("" + maxResultCount) + "&"; 
      url_ = url_.replace(/[?&]$/, "");

      return this.http.get<any>(url_);
  }

   /**
     * @param input (optional) 
     * @return Success
     */
    createOrEdit(input: any | null | undefined ): Observable<CreateOrEditPatientDto> {
      let url_ =  BaseUrl.baseUrl + "/api/services/app/Patients/CreateOrEdit";
      url_ = url_.replace(/[?&]$/, "");
      
      return this.http.post<CreateOrEditPatientDto>( url_,input); 
      }
      

          /**
     * @param id (optional) 
     * @return Success
     */
    getPatientForEdit(id: number | null | undefined): Observable<GetPatientForEditOutput> {
      let url_ = BaseUrl.baseUrl + "/api/services/app/Patients/GetPatientForEdit?";
      // var headers_object = new HttpHeaders().set("Authorization", "bearer " + userToken);
      if (id !== undefined)
          url_ += "Id=" + encodeURIComponent("" + id) + "&"; 
      url_ = url_.replace(/[?&]$/, "");
      return this.http.get<GetPatientForEditOutput>(url_);
  }

      /**
     * @param id (optional) 
     * @return Success
     */
    delete(id: number | null | undefined) : Observable<void> {
      let url_ = BaseUrl.baseUrl + "/api/services/app/Patients/Delete?";
      // var headers_object = new HttpHeaders().set("Authorization", "bearer " + userToken);
      if (id !== undefined)
          url_ += "Id=" + encodeURIComponent("" + id) + "&"; 
      url_ = url_.replace(/[?&]$/, "");

      return this.http.delete<any>(url_);
  }

  }


